import axios from 'axios'
import Api from './Api'

let instance = axios.create({
    baseUrl: '/api',
    timeout: 1000
})
const Http = {}
for (let key in Api) {
    let api = Api[key]
    let res = {}
    //params 请求参数，get/delete (url)，put/patch/post (data)
    // isFormData 标识是否form-data请求
    // config 请求配置参数
    Http[key] = async function (
        params,
        isFormData = false,
        config = {}
    ) {
        let url = api.url
        let method = api.method
        let newParams;
        // 判断content-type是否是form-data
        if (params && isFormData) {
            newParams = new FormData()
            for (let key in params) {
                newParams.append(key, params[key])
            }
        } else {
            newParams = params
        }
        if (method === 'put' || method === 'patch' || method === 'post') {
            try {
                res = await instance[method](url, newParams, config)
            } catch (err) {
                res = err
            }
        } else if (method === 'get' || method === 'delete') {
            console.log(instance)
            console.log(url,method,newParams)
            try {
                config.params = newParams
                res = await instance[method](url, config)
            } catch (err) {
                res = err
            }
        }
        return res
    }
}
// 拦截器的添加
instance.interceptors.request.use(config => {
    // 发起请求前做些什么
    /*this.$notify.loading({
        mask: true,
        duration: 0, // 一直存在
        forbidClick: true, // 禁止点击
        message: '加载中...'
    })*/
    return config
}, err => {
    // 请求失败
    // Toast.clear()
    this.$notify('请求错误，请稍后重试', err)
})
instance.interceptors.response.use(res => {
    // 响应成功后做什么
    // Toast.clear()
    return res.data
}, err => {
    // 响应失败
    // Toast.clear()
    this.$notify('响应错误，请稍后重试',err)
})
export default Http;
